.about {
  font-family: $main-font;
  background-color: $main-bgc-light;
  padding-top: 40px;

  @media screen and (max-width: 767px) {
    background-image: url(/src/images/mobile/bg-milk-mob-1.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/mobile/bg-milk-mob-2.png);
    }

    margin: 0 auto;
    background-repeat: no-repeat;
    background-size: contain;
    background-position-y: 574px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 20px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 60px;

    background-image: url(../images/desktop/bg-milk-dt-1.png);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/desktop/bg-milk-dt-2.png);
    }

    background-position: bottom;
    background-repeat: no-repeat;
  }
}

.container {
  @media screen and (min-width: 320px) {
    max-width: 320px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
  }
}

.about__label {
  color: $accent-light-color;
  font-family: $second-font;
  margin-bottom: 15px;
  text-align: center;

  font-size: 18px;
  line-height: 1.17;
  text-transform: uppercase;

  @media screen and (min-width: 768px) {
    margin-bottom: 16px;
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    letter-spacing: 0.04em;
  }
}

.about__header {
  color: $accent-color;
  font-family: $second-font;
  margin-bottom: 32px;
  text-align: center;

  font-weight: 400;
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
    font-size: 48px;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 60px;
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  }
}

.about-content {
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
  }
}

.about-content__img {
  margin-bottom: 40px;
  width: 100%;

  @media screen and (min-width: 768px) {
    width: 336px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    width: 501px;
  }
}

.about-text {
  position: relative;
  width: 220px;

  @media screen and (min-width: 768px) {
    width: 334px;
    margin-left: 32px;
  }

  @media screen and (min-width: 1280px) {
    width: 463px;
    margin-left: 139px;
  }
}

.about-text__top {
  color: $dark-color;
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.76;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 32px;
    font-size: 16px;
    letter-spacing: 0.04em;
  }
}

.about-text__bottom {
  color: $additional-color;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
    font-size: 14px;
    line-height: 1.76;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1280px) {
    line-height: 1.94;
  }
}

.about__btn {
  width: 176px;
  height: 44px;
  border-radius: 22px;
  border: none;

  font-family: inherit;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;

  color: $accent-color;
  background-color: $main-color;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;

  &:hover,
  &:focus {
    background-color: $hover-btn-color;
  }

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.31;
  }

  &:hover + .about-text__wrap {
    opacity: 1;
  }
}

.btn-text {
  margin-right: 13px;
}

.about-text__wrap {
  position: absolute;
  top: 0;
  height: 85%;
  padding: 20px 20px;
  overflow: hidden;
  opacity: 0;
  transition: opacity 450ms ease-in-out;
  background-color: $mobile-bgc;
  border-radius: 10px;
  box-shadow: 0px 2px 5px #bd9d6dd3;

  @media screen and (min-width: 768px) {
    padding: 30px 20px;
    height: 80%;
  }

  @media screen and (min-width: 1280px) {
    padding: 40px 20px;
    height: 75%;
  }
}

.about-text__subtext {
  font-family: $main-font;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  color: $main-color;
  text-align: center;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: 1.31;
  }
}
